#root {
  height: 100%;
  width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  display:inline-block;
  float:left;
  vertical-align: middle;
  height: 80px;
}

.App-header {
  clear: both;
  position: relative;
  background-color: #222;
  padding: 20px;
  color: white;
  height: 80px;
}

.companyTitle {
  float: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  vertical-align:middle;
  height: 100%;
  margin: 0 10px;
  padding: auto;
  overflow: hidden;
}

.companyName {
  display: inline-block;
  vertical-align: middle;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.headerIcon {
  clear: both;
  float: left;
  display: inline-block;
  width: auto;
  height: auto;
}

.navMenu {
  float:right;
  display: inline-block;
  list-style-type: none;
}

.navMenu li {
  float: left;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap:nowrap;
      flex-wrap:nowrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  margin: auto;
  padding: 1em;
}

.mainWindow {
  clear: both;
  display: -ms-flexbox;
  display: flex;
  width: 85%;
  position: relative;
  margin: auto;
  padding: auto;
  background-color: green;
}

.Footer {
  clear: both;
  display:-ms-flexbox;
  display:flex;
  float:left;
  position: absolute;
  bottom: 0;
  left: 7.5%;
  height: auto;
  width: 85%;
  background-color: grey;
  margin: auto;
  padding: auto;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



